
import store from '../../../assets/js/shareData/share'
import Prompt from '../../tips/tankuang'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "flight",
components: {
XHeader,
Prompt
},
data:function() {
return{
flightNum:'',
flightDate:'',
error:''
,           flag:false,
getOn:'',
getOff:'',
onTime:''
}
},
mounted:function() {
this.flightDate = this.$store.state.puFlightDate
this.flightNum = this.$store.state.puFlightNum
},
methods:{
validate() {
if(!this.flightNum) {
this.flag = true;
this.error = "请输入航班信息"
return
}
if(!this.flightDate) {
this.flag = true;
this.error = "请输入航班信息"
return
}
this.$store.state.puFlightNum = this.flightNum;
this.$store.state.puFlightDate = this.flightDate;
this.sendData();
},
sendData() {
this.$router.push("/pickUpStep")
}
},
store
}



