import '../../assets/js/base.js'
import store from '../../assets/js/shareData/share'
export default {
  data:function() {
    return {
      ming:"",
      error:"",
      flag:false,
      path:"",
      userShow:true
    }
  },
  methods: {
    validate() {
      if (!this.ming) {
        this.flag = true;
        this.error = "用户名不能为空"
        let timer = setTimeout(()=>{
          this.flag = false;
          clearTimeout(timer);
        },1000)
        return
      }
      let word = /^([\u4E00-\u9FA5]|\w){2,8}$/;//验证名字
      if (!word.test(this.ming)) {
        this.flag = true;
        this.error = "用户名长度必须在2-8位之间"
        let timer = setTimeout(()=>{
          this.flag = false;
          clearTimeout(timer);
        },1000)
        return
      }
      this.flag = false;
      this.sendData()
    },
    sendData() {
      this.$http.post(this.$store.state.baseUrl + "post-name", {
        username: this.ming
      }, {emulateJSON: true}).then((response) => {
        if (response.data == "true") {
          console.log(response.data);
          this.$router.push("/regist2")
        } else {
          this.flag = true;
          this.error = response.data
        }
      })
    },
    toRegist2() {
      this.$router.push("/regist2");
    },
    isNameShow() {
      if(!this.ming) {
        this.userShow = true;
      }
    }

  },
  store
}



