
import Button from 'element-ui'
import store from '../../assets/js/shareData/share'
import Prompt from '../tips/tankuang'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name:"Tou",
components: {
XHeader,
Button,
Prompt
},
data(){
return{
flag:false,
errors:'',
oid:'',
time:'',
getOn:'',
getOff:'',
costTime:'',
distance:0,
value3: false,
payWay:'现金支付',
minCost:0,
maxCost:0,
origin:""
}
},
created:function() {
if(this.$route.query.oid) {
this.oid = this.$route.query.oid
this.origin = this.$route.query.origin
}
this.getUser()
this.$socket.emit("connect", "")
},
mounted:function(){
let This = this;
this.$http.post(This.$store.state.baseUrl+"toEnsureOrders", {oid:this.oid,origin:this.origin},{emulateJSON:true}).then(function(res) {
if(res.data) {
This.oid = res.data.oid;
This.time = res.data.onTime;
This.getOn = res.data.getOn;
This.getOff = res.data.getOff;
This.costTime = res.data.duration;
This.distance = res.data.distance;
This.minCost = parseFloat(res.data.predictCost.minCost).toFixed(2);
This.maxCost = parseFloat(res.data.predictCost.maxCost).toFixed(2)
}
})
},
sockets:{
connect() {
console.log("ensureOrders用户端已建立连接")
this.$socket.emit("groupId", this.oid);
this.$socket.emit(this.oid + "chatGroup");
}
},
methods: {
open() {
this.$alert('到位打车均为线上支付，起步价为15元，超过3公里后，每公里加收3元' +
'如司机有行为不当、另加收费用等情况，' +
'请您拨打客服电话110', '收费说明', {
confirmButtonText: '确定',
});
},
validate() {
if(!this.payWay) {
this.errors = "请选择付款方式"
setTimeout(()=>{
this.errors='';
},1000)
return
}
this.sendData();
},
sendData() {
let This = this
let order = {
oid:this.oid,
getOn:this.getOn,
getOff:this.getOff,
onTime:this.time
/*costTime:this.costTime,
distance:this.distance,
minCost:this.minCost,
maxCost:this.maxCost*/
}
console.log("user+++++++++++++++++++++++++++")
console.log(this.$store.state.user)
this.$socket.emit("orderData", {order:order,user:this.$store.state.user})
this.$http.post(this.$store.state.baseUrl+"ensureOrders", {payWay:this.payWay,oid:this.oid}, {emulateJSON:true}).then(function(res) {
if(res.data) {
This.$store.state.isRefresh = true;
This.$router.push({
path:"/noStartedOrder",
query:{
oid:this.oid,
origin:this.origin
}
});
}
})
},
getUser() {
this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
if(typeof response.data == "object") {
this.$store.state.user = response.data
}
})
},
isChecked() {
if(this.payWay) {
return this.payWay === "线上支付";
}
return false;
}
},
store
}



