import '../../assets/js/base.js'
import store from '../../assets/js/shareData/share'
export default {
  data:function() {
    return {
      pwd:"",
      error:"",
      flag:false,
      userShow:true
    }
  },
  methods:{
    validate() {
      if (!this.pwd) {
        this.flag = true;
        this.error = "请填写密码"
        let timer = setTimeout(()=>{
          this.flag = false;
          clearTimeout(timer);
        },1000)
        return
      }
      let pPattern = /^[a-zA-Z0-9]{5,15}$/;
      if (!pPattern.test(this.pwd)) {
        this.flag = true;
        this.error = "您输入的密码不符合规定（密码由：数字、字母、下划线、特殊符号组成,长度必须大于5位)"
        let timer = setTimeout(()=>{
          this.flag = false;
          clearTimeout(timer);
        },3000)
        return
      }
      this.flag = false;
      this.sendData()
    },
    sendData() {
      this.$http.post(this.$store.state.baseUrl+"post-password", {
        password: this.pwd
      }, {emulateJSON: true}).then((response) => {
          this.$router.push("/regist3")
      })
    },
    isUserShow() {
      if(!this.pwd) {
        this.userShow = true;
      }
    },
    toRegist1() {
      this.$router.push("/regist3")
    }
  },
  store
}



