import {Button,DateTimePicker,Main} from 'element-ui'
import store from '../../../assets/js/shareData/share'
import Jieji from '@/components/public/jieheader'
import Tou from '@/components/public/header'
import Prompt from "../../tips/tankuang"
export default {
  components: {
    Button,
    DateTimePicker,
    Tou,
    Main,
    Jieji,
    Prompt
  },
  data() {
    return {
      form: {
        name: '',
        region: '',
      },
      pickerOptions1: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
      error:'',
      flag:false,
      onTime:'',
      getOn: '',
      getOff: '',
      isBackAndForth:false,
      flightNum:"",
      flightDate:''
    }
  },
  mounted:function() {
    this.getUser()
    this.onTime = this.$store.state.puOnTime
    this.flightDate = this.$store.state.puFlightDate
    this.flightNum = this.$store.state.puFlightNum
    this.getOn = this.$store.state.puGetOn
    this.getOff = this.$store.state.puGetOff
  },
  methods:{
    toFlight() {
      this.$router.push("/pickUpFlight")
    },
    validate:function() {
      if(!this.flightNum) {
        this.flag = true;
        this.error = "请填写航班号"
        let timer = setTimeout(()=>{
          this.error = ""
          clearTimeout(timer);
        },1000)
        return
      }
      if(!this.flightDate) {
        this.flag = true;
        this.error = "请填写航班号"
        let timer = setTimeout(()=>{
          this.error = ""
          clearTimeout(timer);
        },1000)
        return
      }
      if (!this.onTime) {
        this.flag = true
        this.error = "请输入用车时间"
        let timer = setTimeout(()=>{
          this.error = ""
          clearTimeout(timer);
        },1000)
        return
      }
      if (!this.getOn) {
        this.flag = true
        this.error = "请输入上车地点"
        let timer = setTimeout(()=>{
          this.error = ""
          clearTimeout(timer);
        },1000)
        return
      }
      if (!this.getOff) {
        this.flag = true
        this.error = "请输入下车地点"
        let timer = setTimeout(()=>{
          this.error = ""
          clearTimeout(timer);
        },1000)
        return
      }
      this.sendData()
    },
    preserve() {
      this.$store.state.puOnTime = this.onTime;
    },
    sendData() {
      this.$http.post(this.$store.state.baseUrl+"pickUpOrder", {
        onTime: this.onTime.toString(),
        getOn: this.getOn,
        getOff: this.getOff,
        isBackAndForth:this.isBackAndForth,
        flightNum:this.flightNum,
        flightDate:this.flightDate
      }, {emulateJSON: true}).then((response) => {
        console.log(response.data)
        if (typeof response.data == "object") {
          console.log("response data : " + response.data.oid);
          this.$router.push({
            path:"/pickUpStep1",
            query:{
              oid: response.data.oid
            }
          })
        } else {
          this.flag = true;
          this.error = response.data
          let timer = setTimeout(()=>{
            this.flag = false;
            clearTimeout(timer);
          },1000)
        }
      })
    },
    toGetOn() {
      this.$router.push("/pickUpGetOn")
    },
    toGetOff() {
      this.$router.push("/pickUpGetOff")
    },
    getUser() {
      this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
        if(typeof response.data == "object") {
          this.$store.state.user = response.data
        }
      })
    }
  },
  store
}



