
import store from '../../../assets/js/shareData/share'
import Prompt from '../../tips/tankuang'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "flight",
components: {
XHeader,
Prompt
},
data:function() {
return{
flightNum:'',
flightDate:'',
error:'',
flag:false,
getOn:'',
getOff:'',
onTime:''
}
},
mounted() {
this.flightDate = this.$store.state.doFlightDate
this.flightNum = this.$store.state.doFlightNum
},
methods:{
validate() {
if(!this.flightNum) {
this.flag = true;
this.error = "请输入航班信息"
setTimeout(()=>{
this.error='';
},1500)
return
}
if(!this.flightDate) {
this.flag = true;
this.error = "请输入航班信息"
setTimeout(()=>{
this.error='';
},1500)
return
}
this.$store.state.doFlightNum = this.flightNum;
this.$store.state.doFlightDate = this.flightDate;
this.sendData();
},
sendData() {
this.$router.push("/dropOffStep")
}
},
store
}



