  import "../../assets/js/base.js"
  import Prompt from '../tips/tankuang'
  import store from '../../assets/js/shareData/share'
    export default {
      name: '',
      components:{
        Prompt
      },
      data() {
        return {
          showPsw:false,          //控制’输入密码‘的输入框显示
          showPic:false,          //控制显示更改图片
          showCan:false,          //显示是否退出登录
          isEdit:false,
          user:{
            username:'',
            email:'',
            phoneNum:'',
            commonAddress:[
              {name:'',address:''}
            ],
            avatar:'touxiang_03.png'
          },  //用户信息
          inputPsw:'',
          flag:false,
          files:'',
          error:'',
          starArr:[],
          negativeStarArr:[]
        }

      },
      mounted(){
        this.send();
        this.flag = this.$store.state.editPerCen;
      },
      methods:{
        send(){
          this.$http.get(this.$store.state.baseUrl+'getState').then(function (response) {
            let resData = response.data;
            if(resData){
              this.user = resData;
            }else{
              this.$router.push('/login');
            }
          })
        },
        showStar() {
          let starDeg = parseInt(user.star)
          for(let i = 0; i < starDeg; i++) {
            this.starArr.push(i)
          }
          for(let j = 0; j < 5 - starDeg; j++) {
            this.negativeStarArr.push(j)
          }
        },
        edit(){
          this.showPsw=!this.showPsw;
        },
        showPicEdit(){
            this.showPic=!this.showPic;
        },
        showCancel(){
            this.showCan=!this.showCan;
        },
        //图片发生变化的时候提交
        uploadAvatar(event){
            if(event.target.files.length>0){
              this.files = event.target.files[0];   //提交的图片
              let data  = {};
              let files = this.files;
              let param = new FormData(); //创建form对象
              if(files!=''){
                param.append('file', files,files.name); //单个图片 ，多个用循环 append 添加
              }else{
                this.$message.error('请添加图片');
              }
              param.append('param', JSON.stringify(data));//添加form表单中其他数据
              let config = {
                headers:{'Content-Type':'multipart/form-data'}
              };  //添加请求头
              this.$http.post(this.$store.state.baseUrl+'avatarUpload',param,config)
                .then(response => {
                  let ret = response.data;
                  if(ret.status){
                    //清空数据
                    this.user.avatar = ret.url;
                    this.files = [];
                    this.showPic=!this.showPic;
                  }else{
                    alert('上传失败')
                  }
                })
            }
        },
        //验证密码
        checkPassword(){
          if(this.inputPsw){
            this.$http.post(this.$store.state.baseUrl+"editPerInfo",{
              password:this.inputPsw
            },{emulateJSON: true}).then((response) => {
              if(response.data){
                this.$store.state.editPerCen = true;
                this.flag=this.$store.state.editPerCen;
                this.showPsw=!this.showPsw;
// 只要不刷新，一分钟以内都可以修改个人中心的资料
                let timer = setTimeout(()=>{
                  this.$store.state.editPerCen=false;
                  clearTimeout(timer)
                },60000)
              }else {
                alert('密码不正确');
              }
            })
          }else {
            alert('不能为空')
          }
        },
        //登出
        signOut(){
          this.$http.get(this.$store.state.baseUrl+"signOut").then((response) => {
            if(response.data){
              this.$router.push('/');
            }else {
              alert('请重新尝试');
            }
          });
        },
        //是否可以修改用户名
        editUsername(){
          if(this.user.username){
            if(this.flag){
              this.$http.post(this.$store.state.baseUrl+"editUsername",{username:this.user.username},{emulateJSON: true}).then((response) => {
                alert(response.data);
              })
            }else {
              this.error = '点击编辑，输入密码才可以修改个人资料';
              setTimeout(()=>{
                this.error='';
              },1500)
            }
          }else {
            alert('用户名不能为空')
          }
        },
        //动态路由
        youxiangxiugai(){
          if(this.flag){
            return '/youxiangxiugai'
          }else {
            return ''
          }
        },
        xiugaimima(){
          if(this.flag){
            return '/xiugaimima'
          }else {
            return ''
          }
        },
        comAddress(){
          if(this.flag){
            return '/comAddress'
          }else {
            return ''
          }
        },
        zidingyi(){
          if(this.flag){
            return '/custom'
          }else {
            return ''
          }
        }
      },
      filters:{
        urlHandle(value,url){
          if(!value) return '';
          return url+'avatar/'+value;
        },
        addressFilter(value){
          if(value.length){
            return value[0].address;
          }else {
            return '请设置常用地址'
          }
        }
      },
      store
    }



