import Tou from '@/components/public/header'
import store from '../../assets/js/shareData/share'
  export default {
    components: {
      Tou
    },
    data() {
      return {
        options1: [{
          value: '1',
          label: '1'
        }, {
          value: '2',
          label: '2'
        }, {
          value: '3',
          label: '3'
        }, {
          value: '4',
          label: '4'
        }],
        options2: [{
          value: '0',
          label: '0'
        },{
          value: '1',
          label: '1'
        }, {
          value: '2',
          label: '2'
        }, {
          value: '3',
          label: '3'
        }, {
          value: '4',
          label: '4'
        }],
        pNum:0,
        luggages:0,
        other:'',
        error:"",
        flag:false,
        minCost:0,
        maxCost:0,
        oid:''
      }
    },
    created:function(){
      if(this.$route.query.oid) {
        this.oid = this.$route.query.oid
      }
    },
    mounted:function() {
      this.$http.post(this.$store.state.baseUrl+"getPredictCost",{oid:this.oid},{emulateJSON:true}).then(function(response){
        if(response.data) {
          this.minCost = parseFloat(response.data.minCost).toFixed(2)
          this.maxCost = parseFloat(response.data.maxCost).toFixed(2)
        }
      })
    },
    methods: {
      open() {
        this.$alert('大件行李为大于等于32*46*70的物品，四人乘车大件行李数不得超过2件，' +
          '三人出行大件行李不得超过三件',
          '提示', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      validate(){
        console.log("下一步。。。。。。")
        if (!this.pNum) {
          this.flag = true;
          this.error = "请输入上车人数"
          let timer = setTimeout(()=>{
            this.flag = false;
            clearTimeout(timer);
          },1000)
          return
        }
        if (!this.luggages) {
          this.flag = true;
          this.error = "请输入行李数"
          let timer = setTimeout(()=>{
            this.flag = false;
            clearTimeout(timer);
          },1000)
          return
        }
        this.sendData()
      },
      sendData() {
        var This = this
        this.$http.post(this.$store.state.baseUrl+"getMoreMessages", {pNum:this.pNum,luggages:this.luggages,other:this.other,oid:this.oid},
          {emulateJSON:true}).then(function(res) {
            console.log(res.data)
          if(typeof res.data == "object") {
            This.$router.push({
              path:"/ensureOrders",
              query:{
                oid:res.data.oid,
                origin:"normal"
              }
            })
          }
        })
      }
    },
    store
  }



