import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
      name: '',
    data(){
      return {
        coupon:{}
      }
    },
    created(){
        this.getCoupons()
    },
    methods:{
        getCoupons(){
          this.$http.get(this.$store.state.baseUrl+'getState').then(res=>{
            this.$store.state.user.coupon = res.data.coupon;
            this.coupon = res.data.coupon;
          })
        }
    },
    store
  }



