import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
      name: "order-help",
    data(){
      return{
        showPrice:false,
        hisOrderDetails:{}
      }
    },
    mounted(){
      this.send();
    },
    methods:{
      send(){
        if(this.$store.state.hisOrderDetails.oid){
          this.hisOrderDetails = this.$store.state.hisOrderDetails;
        }else{
          this.$router.push('/historyOrders')
        }
      },
      goLost(){
        this.$router.push('/lost')
      }
    },
    filters:{
      urlHandle(value,url){
        if(!value) return '';
        return url+'avatar/'+value;
      },
      currency(value){
        if(!value) return 0.00;
        let num = value.toString();
        let valArr = num.split('.');
        if(valArr.length<=1){
          return num+'.00';
        }else if(valArr[1].length<2){
          return num+'0';
        }else {
          return valArr[0]+'.'+valArr[1].substring(0,2);
        }
      }
    },
    store
  }



