  import "../../assets/js/base.js"
  import store from '../../assets/js/shareData/share'
  export default {
    name: '',
    data(){
      return{
        payOrder:{},
        coupon:{},
        time:60,
        timer:{}
      }
    },
    mounted(){
      this.autoPay();
      this.getCoupons();
      this.payOrder=this.$route.params.payOrder;
/*      this.payOrder = {
        realCost:100,
        oid:'',
        driverName:'',
        driverPhoneNum:'12345'
      }*/
    },
    methods:{
      getCoupons(){
        this.$http.get(this.$store.state.baseUrl+'getState').then(res => {
          this.$store.state.user.coupon = res.data.coupon;
          this.coupon = res.data.coupon;
        })
      },
      selectCoupon(index){

        if(confirm('确定使用优惠券？')){
          clearInterval(this.timer);
          this.$http.post(this.$store.state.baseUrl+'useCoupon',{index:index,realCost: this.payOrder.realCost},{emulateJSON:true}).then(res => {
            if(res.data.status){
              this.getCoupons();
              this.payOrder.originCost = this.payOrder.realCost;
              let discount = this.payOrder.realCost - res.data.realCost;
              this.payOrder.realCost = res.data.realCost;
              this.payOrder.discount = discount;
              /*console.log("this.payOrder.originCost: " + this.payOrder.originCost)
              console.log("this.payOrder.realCost: " + this.payOrder.realCost)
              console.log("this.payOrder.discount: " + this.payOrder.discount)*/

              this.$router.push({
                name:'payOrder',
                params:{payOrder:this.payOrder}
              })
            }else {
              this.payOrder.discount =0;
              this.payOrder.originCost = this.payOrder.realCost;
              this.$router.push({
                name:'payOrder',
                params:{payOrder:this.payOrder}
              })
            }
          })
        }
      },
      noPayWithCoupon(){

        if(confirm('确定不使用优惠券？')){
          clearInterval(this.timer);
          this.payOrder.discount =0;
          this.payOrder.originCost = this.payOrder.realCost;
          this.$router.push({
            name:'payOrder',
            params:{payOrder:this.payOrder}
          })
        }
      },
      autoPay(){
        this.timer = setInterval(()=>{
          this.time --;
          if(this.time<=0){
            clearInterval(this.timer);
            this.payOrder.discount =0;
            this.payOrder.originCost = this.payOrder.realCost;
            this.$router.push({
              name:'payOrder',
              params:{payOrder:this.payOrder}
            })
          }
        },1000)
      },
      gotoBack(){
        clearInterval(this.timer);
        this.payOrder.discount =0;
        this.payOrder.originCost = this.payOrder.realCost;
        this.$router.push({
          name:'payOrder',
          params:{payOrder:this.payOrder}
        })
      },
    },
    store
  }



