  import store from '../assets/js/shareData/share'
import  '../assets/js/base.js'
import NoLogin from '@/components/indexState/noLogin'
import NoOrder from '@/components/indexState/noOrder'
import HasOrder from '@/components/indexState/hasOrder'
export default {
  components: {
    NoLogin,
    NoOrder,
    HasOrder
  },
  name: 'hello-word',
  data: function () {
    return {
      loginMsg: false,          //true:已登录,false：没登录
      isCollapse: true,
      hasOd:false,              //true:有订单，false：没订单
      city:'',
      ip:'',
      user:{
        username:'用户名',
        avatar:'',
        star:'0'
      },
      baseUrl:'',
      time:'',
      driverName:'',
      realCost:0,
      driverPhoneNum:'',
      carCode:'',
      extend:false,
      imgArr:[{active:true},{active:true},{active:true},{active:true},{active:true}],
      ordersArr:[],
      isShowMap:false
    }
  },
  mounted: function () {
    this.selectShow();
  },
  methods: {
    goHisOrders(){
      this.$router.push('/historyOrders')
    },
    selectShow: function () {
      this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
        if(response.data) {
          let resData = response.data;
          this.user = resData;
          this.$store.state.user = this.user;
          this.$store.state.invitationCode = resData.invitationCode;
          this.$store.state.coupon = resData.coupon;
          this.baseUrl = this.$store.state.baseUrl;
          if( resData.historyOrders.length) {
            this.$store.state.historyOrders = resData.historyOrders;
          }
          if(resData.orders.length){
             for(var i = 0; i < resData.orders.length; i++) {
               if(resData.orders[i].state == 1) {
                 this.isShowMap = true;
                 this.ordersArr.push(resData.orders[i])
                 this.hasOd = true;
                 break;
               }
             }
          }else {
            this.hasOd = false;
          }
          let This = this;
          this.loginMsg = true;
          this.preserve();
          this.locate()


        }else {
          this.loginMsg = false;
          this.user ={
            username:'用户名',
            avatar:'',
            star:'0'
          };
          this.$store.state.user = this.user;
          this.$store.state.invitationCode='';
          this.$store.state.coupon = '';
          this.$store.state.historyOrders='';
        }
      })
    },
    logout: function () {
      this.$http.get(this.$store.state.baseUrl+"signOut").then((response) => {
        this.loginMsg = false;
      })
    },
    eitherCall() {
      if(this.loginMsg) {
          this.$router.push("/step")
      }
    },
    locate(){
      var map = new BMap.Map("baiduMap");
      var point = new BMap.Point(116.331398,39.897445);
      map.centerAndZoom(point,12);
      let This = this;
      function myFun(result){
        var cityName = result.name;
        map.setCenter(cityName);
        This.city = cityName;
      }
      var myCity = new BMap.LocalCity();
      myCity.get(myFun);
      this.$http.get(this.$store.state.baseUrl+"getCity", {params:{city:this.city}}).then(function(response) {
        console.log(response.data);
      })
      if(this.isShowMap) {
        this.showMap()
      }
     /* this.$jsonp("http://pv.sohu.com/cityjson?ie=utf-8").then(function(json) {
        console.log(json);
        if(json) {
          var returnData = json;
          var jsonData = JSON.parse(returnData.substring(returnData.indexOf("{"), returnData.length - 1));
          this.ip = jsonData.cip;
          this.city = jsonData.cname;
          this.$http.get(this.$store.state.baseUrl+"getCity", {params:{city:this.city,ip:this.ip}}).then(function(response) {
            console.log(response.data);
          })
          if(this.isShowMap) {
            this.showMap()
          }
        }
      })*/
    },
    preserve() {
      console.log("失去焦点")
      this.$store.state.onTime = this.onTime;
    },
    handleOpen(key, keyPath) {},
    handleClose(key, keyPath) {},
    toPerCen(){
      this.$router.push('/personalCenter')
    },
    //跳转到分享优惠码页
    goShareCoupon(){
      this.$router.push('/shareCoupon')
    },
    goCoupon(){
      this.$router.push('/coupon')
    },
    eitherPick(){
      if (this.loginMsg) {
        this.$router.push("/pickUpStep")
      }
    },
    eitherDrop(){
      if (this.loginMsg) {
        this.$router.push("/dropOffStep")
      }
    },
    handleToggleDrawer() {
      this.$refs.drawerLayout.toggle();
    },
    showMap() {
      var map = new BMap.Map("baiduMap");
      var point = new BMap.Point(116.331398,39.897445);
      map.centerAndZoom(point,12);

      function myFun(result){
        var cityName = result.name;
        map.setCenter(cityName);
      }
      var myCity = new BMap.LocalCity();
      myCity.get(myFun);

    },
    goToNoStarted(oid) {
      console.log("hasOrder,,,,,,,,,,,,,,,,,,,,点击事件")
      this.$router.push({
        path:"/noStartedOrder",
        query:{
          oid:oid,
          origin:"normal"
        }
      })
    }
  },
  filters: {
    urlHandle(value, url) {
      if (!value) return '';
      return url + 'avatar/' + value;
    },
    currency(value) {
      if (!value) return 0.00;
      let num = value.toString();
      let valArr = num.split('.');
      if (valArr.length <= 1) {
        return num + '.00';
      } else if (valArr[1].length < 2) {
        return num + '0';
      } else {
        return valArr[0] + '.' + valArr[1].substring(0, 2);
      }
    }
  },
  store
}



