
import store from '../../../assets/js/shareData/share'
import Prompt from '../../tips/tankuang'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader,
Prompt
},
data:function() {
return {
alikeAddressList:[],
onAddress:'',
isShow:false,
city:'',
ip:'',
longitude:0,
latitude:0,
offAddress:'',
onTime:"",
flightNum:"",
flightDate:"",
commonAddressList:[],
historyAddressList:[],
error:''
}
},
created:function() {
this.onAddress = this.$store.state.puGetOn;
this.getUser()
this.getCityAndIP();
},
methods:{
toUp(index){
this.onAddress = this.$store.state.user.commonAddress[index].address
this.preserve()
this.sendAddress();
},
toUpMore(index){
this.onAddress = this.alikeAddressList[index].name
this.preserve()
this.sendAddress();
},
preserve() {
this.$store.state.puGetOn = this.onAddress;
},
sendAddress() {
let This = this;
this.getPosition()
.then(function(data) {
console.log(data);
This.longitude = data.result.location.lng;
This.latitude = data.result.location.lat;
This.$http.post(This.$store.state.baseUrl+"toPickUpStep",{onAddress:This.onAddress,onLng:This.longitude,onLat:This.latitude},{emulateJSON: true}).then(function(res){
if(res.data) {
This.$router.push("/pickUpStep");
}
})
}).catch(reason =>{
this.error = '该地址不存在,请重新输入';
setTimeout(()=>{
this.error='';
},1000)
});
},
toUpHistory(index){
this.onAddress = this.$store.state.user.historyOrders[index].onAddress;
this.preserve()
this.sendAddress();
},
autoSearch() {
console.log("ffef")
this.isShow = true;
},
sendToCustom() {
this.$router.push({
path:'/custom',
query:{
source:"onAddress"
}
})
},
getCityAndIP() {
this.$http.get(this.$store.state.baseUrl+"sendCityAndIp").then(function(response) {
if(response.data) {
this.city = response.data.city.substring(0,response.data.city.indexOf("市"));
}
})
},
findAlike() {
let This = this;
this.$jsonp
("http://api.map.baidu.com/place/v2/suggestion?" +
"query="+ this.onAddress +"&region=" + this.city +
"city_limit=true&output=json&ak=M1pZKOGy4myBv0Lh6jKsMZvoIopBYC1i").then(function(data) {
if(data) {
console.log(data);
This.alikeAddressList = data.result
This.isShow = true;
}
})

},
getPosition() {
var This = this;
let p = new Promise(function (resolve, reject) {
This.$jsonp("http://api.map.baidu.com/geocoder/v2/?address=" + This.onAddress +
"&output=json&ak=M1pZKOGy4myBv0Lh6jKsMZvoIopBYC1i").then(function(data) {
if(data.status) {
reject(false)
}else{
resolve(data)
}
})
})
return p;
},
submit() {
this.preserve()
this.sendAddress();
},
getUser() {
this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
if(typeof response.data == "object") {
this.$store.state.user = response.data
}
})
}
},
store
}



