import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
      name: '',
    data(){
      return{
        showPrice:false,
        qqHref:''
      }
    },
    created(){
      this.shareQQ();
    },
    methods:{
      shareWx(){
        this.$http.post(this.$store.state.baseUrl+"shareWx",{url:location.href}).then(response=>{
          let resData = response.data;
          wx.config({
            debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: resData.appId, // 必填，公众号的唯一标识
            timestamp: resData.timestamp, // 必填，生成签名的时间戳
            nonceStr: resData.nonceStr, // 必填，生成签名的随机串
            signature: resData.signature,// 必填，签名
            jsApiList: resData.jsApiList // 必填，需要使用的JS接口列表
          });
          wx.ready(function(){
            wx.onMenuShareTimeline({
              title: '到位打车',
              link: 'localhost:8000',
              imgUrl:'',
              success: function () {
                // 用户确认分享后执行的回调函数
                alert('分享到朋友圈成功');
              },
              cancel: function () {
                // 用户取消分享后执行的回调函数
                alert('你没有分享到朋友圈');
              }
            });
            wx.onMenuShareAppMessage({
              title: '到位打车',
              desc: '到位打车真的好',
              link: 'localhost:8000',
              imgUrl: '',
              trigger: function (res) {
                // 不要尝试在trigger中使用ajax异步请求修改本次分享的内容，因为客户端分享操作是一个同步操作，这时候使用ajax的回包会还没有返回
              },
              success: function (res) {
                alert('分享给朋友成功');
              },
              cancel: function (res) {
                alert('你没有分享给朋友');
              },
              fail: function (res) {
                alert(JSON.stringify(res));
              }
            });
          });
          wx.error(function(res){
            // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
              console.log(res);
          });
        })
      },
      shareQQ(){
        var p = {
          url:location.href, /*获取URL，可加上来自分享到QQ标识，方便统计*/
          desc:'', /*分享理由(风格应模拟用户对话),支持多分享语随机展现（使用|分隔）*/
          title:'', /*分享标题(可选)*/
          summary:'', /*分享摘要(可选)*/
          pics:'', /*分享图片(可选)*/
          flash: '', /*视频地址(可选)*/
          site:'', /*分享来源(可选) 如：QQ分享*/
          style:'201',
          width:32,
          height:32
        };
        var s = [];
        for(var i in p){
          s.push(i + '=' + encodeURIComponent(p[i]||''));
        }
        this.qqHref = "http://connect.qq.com/widget/shareqq/index.html?',s.join('&'),'"
      }
    },
    store
  }



