
import Button from 'element-ui'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name:"Songji",
components: {
XHeader,
Button,
},
methods: {
open() {
this.$alert('到位打车均为线上支付，起步价为15元，超过3公里后，每公里加收3元' +
'如司机有行为不当、另加收费用等情况，' +
'请您拨打客服电话110', '收费说明', {
confirmButtonText: '确定',
});
}
}
}



