import {Button,DateTimePicker,Main} from 'element-ui'
import store from '../../../assets/js/shareData/share'
import Songji from "../../public/songheader.vue";
import Prompt from "../../tips/tankuang"
    export default {
      components: {
        Songji,
        Button,
        DateTimePicker,
        Main,
        Prompt
      },
      data() {
        return {
          form: {
            name: '',
            region: '',
          },
          pickerOptions1: {
            shortcuts: [{
              text: '今天',
              onClick(picker) {
                picker.$emit('pick', new Date());
              }
            }, {
              text: '昨天',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24);
                picker.$emit('pick', date);
              }
            }, {
              text: '一周前',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', date);
              }
            }]
          },
          error:'',
          flag:false,
          onTime:'',
          getOn: '',
          getOff: '',
          isBackAndForth:false,
          flightNum:"",
          flightDate:''
        }
      },
      created:function() {
        this.onTime = this.$store.state.doOnTime
        this.flightDate = this.$store.state.doFlightDate
        this.flightNum = this.$store.state.doFlightNum
        this.getOn = this.$store.state.doGetOn
        this.getOff = this.$store.state.doGetOff
        this.getUser()
      },
      methods:{
        validate:function() {
          if(!this.flightNum) {

            this.error = "请填写航班号"
            let timer = setTimeout(()=>{
              this.error = ''
              clearTimeout(timer);
            },1000)
            return
          }
          if(!this.flightDate) {

            this.error = "请填写航班号"
            let timer = setTimeout(()=>{
              this.error = ''
              clearTimeout(timer);
            },1000)
            return
          }
          if (!this.onTime) {

            this.error = "请输入用车时间"
            let timer = setTimeout(()=>{
              this.error = ''
              clearTimeout(timer);
            },1000)
            return
          }
          if (!this.getOn) {

            this.error = "请输入上车地点"
            let timer = setTimeout(()=>{
              this.error = ''
              clearTimeout(timer);
            },1000)
            return
          }
          if (!this.getOff) {
            this.flag = true
            this.error = "请输入下车地点"
            let timer = setTimeout(()=>{
              this.flag = false;
              clearTimeout(timer);
            },1000)
            return
          }

          this.sendData()
        },
        preserve() {
          this.$store.state.doOnTime = this.onTime;
        },
        sendData() {
          this.$http.post(this.$store.state.baseUrl+"dropOffOrder", {
            onTime: this.onTime.toString(),
            getOn: this.getOn,
            getOff: this.getOff,
            isBackAndForth:this.isBackAndForth,
            flightNum:this.flightNum,
            flightDate:this.flightDate
          }, {emulateJSON: true}).then((response) => {
            console.log(response.data)
            if (typeof response.data == "object") {
              console.log("response data : " + response.data.oid);
              this.$router.push({
                path:"/dropOffStep1",
                query:{
                  oid: response.data.oid
                }
              })
            } else {
              this.flag = true;
              this.error = response.data
              let timer = setTimeout(()=>{
                this.flag = false;
                clearTimeout(timer);
              },1000)
              /*this.$set("error", response.data);*/
            }
          })
        },
        toGetOn() {
          this.$router.push("/dropOffGetOn")
        },
        toGetOff() {
          this.$router.push("/dropOffGetOff")
        },
        toFlight() {
          this.$router.push("/dropOffFlight")
        },
        getUser() {
          this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
            if(typeof response.data == "object") {
              this.$store.state.user = response.data
            }
          })
        }
      },
      store
    }



