import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
    name: '',
    data(){
      return {
        historyOrders:[
        ],
        noOrder:true,
        showCheckBox:false,
        showSelectMore:true,
        isChecked:[],
        isCancel:[]
      }
    },
    created(){
      this.send();
    },
    methods:{
      send(){
        if(this.$store.state.historyOrders.length){
          this.historyOrders = this.$store.state.historyOrders;
        }else {
          this.$http.get(this.$store.state.baseUrl+"getState").then(response => {
            let resData = response.data;
            if( resData.historyOrders ) {
              this.$store.state.historyOrders = resData.historyOrders;
              this.historyOrders = resData.historyOrders;
              this.noOrder = true;
            }else {
              this.noOrder = false;
            }
          })
        }
        for (let i=0;i<this.historyOrders.length;i++){
          this.isChecked[i]=false;
          if(this.historyOrders[i].realCost){
            this.isCancel[i] = true;
          }else {
            this.isCancel[i] = false;
          }
        }
        console.log(this.isCancel)
      },
      removeOrder(index){
          this.$http.get(this.$store.state.baseUrl+'removeHisOrder',{params:{ind:index}}).then(res=>{
            this.$store.state.historyOrders = res.data;
            this.historyOrders = res.data;
          });
      },
      removeHisOrders(){
        let oids =[];
        for(let i=0;i<this.isChecked.length;i++){
          if(this.isChecked[i]){
            oids.push(this.historyOrders[i].oid);
          }
        }
        this.$http.post(this.$store.state.baseUrl+'removeHisOrders',oids).then(res=>{
          this.$store.state.historyOrders = res.data;
          this.historyOrders = res.data;
          for (let i=0;i<this.historyOrders.length;i++){
            this.isChecked[i]=false
          }
        })
      },
      goHisDetails(index){
        if(this.historyOrders[index].status){
          this.$router.push('/hisOrderDetails');
        }else {
          console.log('emmmmm')
        }
      }
    },
    filters:{
      urlHandle(value,url){
        if(!value) return '';
        return url+'avatar/'+value;
      },
    },
    store
  }



