import Top from '../Top/top'
import store from '../../assets/js/shareData/share'
export default {
  name: "Tou",
  components: {
    Top
  },
  data () {
    return {
      fangshi: '编辑',
      previence:false,
      input: '',
      chat:false,
      tankuang:false,
      time:'',
      getOn:'',
      getOff:'',
      oid:'',
      costTime:'',
      distance:0,
      minCost:0,
      payWay:'',
      maxCost:0,
      state:'等待接单',
      isTakingOrder:false,
      flag:true,
      isArrival:false,
      realCost:0,
      driverName:'',
      driverPhoneNum:'',
      carCode:'',
      isBackAndForth:Boolean,
      isMessageComing:false,
      messageArr:[],
      carStyle:''
    }
  },
  created:function() {
    if(this.$route.query.oid) {
      this.oid = this.$route.query.oid
      this.origin = this.$route.query.origin
    }
    this.getUser()
    this.getData()
  },
  sockets: {
    connect() {
      console.log("vue用户端已建立通信")
      this.$socket.emit("groupId", this.oid);
      this.$socket.emit(this.oid + "chatGroup");
    },
    takenSuccess(value) {
      console.log("-----------------------------------------------------------------")
      this.driverName = value.username;
      this.driverPhoneNum = value.phoneNum
      this.carCode = value.carCode;
      this.carStyle = value.carStyle;
      this.getDriverOrder()
    },
    arrival(value) {
      console.log("arrival===============")
      this.isArrival = true;
      this.state = "司机已到达"
    },
    over(value) {
      console.log("over连接成功")
        console.log("over" + "相等了")
        let payOrder = {
          oid:this.oid,
          driverName:this.driverName,
          driverPhoneNum:this.driverPhoneNum,
          realCost:this.realCost
        }
        if(this.payWay == "现金支付") {
        payOrder.discount = 0;
          this.$router.push({
            name:"payOrder",
            params:{
              payOrder:payOrder
            }
          })
        }else {
          this.$router.push({
            name:"payWithCoupon",
            params:{
              payOrder:payOrder
            }
          })
        }

    },
    driverMessage(value) {
      console.log(value)
      this.isMessageComing = true;
      this.messageArr.push({message:value,className:"driverMes"})
    }
  },
  methods:{
    gotoBack(){
      this.$router.go(-1)
    },
    fasong(){
      if(this.input){
        console.log("input-------------")
        this.sendToGroup()
      }else{
          this.tankuang = true;
          let timer = setTimeout(()=>{
            this.tankuang = false;
            clearTimeout(timer);
          },1000)
      }
    },
    getUser() {
      this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
        if(typeof response.data == "object") {
          this.$store.state.user = response.data

        }
      })
    },
    getDriverOrder() {
      this.$http.get(this.$store.state.baseUrl + "getDriverOrder", {params:{driverName:this.driverName,oid:this.oid}}).then(function(res) {
        if(res.data) {
          console.log("已获取到司机订单信息=========================")
          console.log(res.data)
          /*this.$store.state.driver.order = res.data;*/
          this.state = "已接单"
          this.realCost = parseFloat(res.data.realCost).toFixed(2);
          this.isTakingOrder = true;
        }
      })
    },
    getData() {
      let This = this;
      this.$http.get(this.$store.state.baseUrl+"toNoStartedOrder", {params:{oid:this.oid,origin:this.origin}}).then(function(res) {
        if(res.data) {
          console.log(res.data);
          This.oid = res.data.oid;
          This.time = res.data.onTime;
          This.getOn = res.data.getOn;
          This.getOff = res.data.getOff;
          This.costTime = res.data.duration;
          This.distance = res.data.distance;
          This.minCost = parseFloat(res.data.predictCost.minCost).toFixed(2);
          This.maxCost = parseFloat(res.data.predictCost.maxCost).toFixed(2);
          This.payWay = res.data.payWay;
          This.isBackAndForth = res.data.isBackAndForth !== "false"
          This.showLine(res.data.onData, res.data.offData)
        }
      })
    },
    showLine(onData, offData) {
      onData.onLat = parseFloat(onData.onLat).toFixed(6)
      onData.onLng = parseFloat(onData.onLng).toFixed(6)
      offData.offLat = parseFloat(offData.offLat).toFixed(6)
      offData.offLng = parseFloat(offData.offLng).toFixed(6)
      var map = new BMap.Map("container");
      map.centerAndZoom(new BMap.Point(onData.onLng, onData.onLat), 14);

      var driving = new BMap.DrivingRoute(map, {
        renderOptions: {
          map: map,
          autoViewport: true
        }
      });
      var start = new BMap.Point(onData.onLng, onData.onLat);
      var end = new BMap.Point(offData.offLng, offData.offLat);
      driving.search(start, end);
    },
    cancel() {
      this.previence = false
        if(this.isTakingOrder) {
          this.$socket.emit("orderCancel", {userData:{username:this.$store.state.user.username,getOn:this.getOn,getOff:this.getOff,
              onTime:this.time}})
        }else {
          this.$socket.emit("orderCancelToAll", {oid:this.oid})
        }
        this.$http.get(this.$store.state.baseUrl + "cancelOrder", {params:{oid:this.oid,driverName:this.driverName}},{emulateJSON:true}).then(function(res) {
          if(res.data) {
            this.$router.push({
              name:"cancelSuccess",
              params:{
                onTime:this.time,
                getOn:this.getOn,
                getOff:this.getOff,
                realCost:this.realCost,
                minCost:this.minCost,
                maxCost:this.maxCost,
                isTakingOrder:this.isTakingOrder
              }
            })
          }
        })
    },
    sendToGroup() {
      this.$socket.emit("userMessage", this.input)
      this.messageArr.push({"message":this.input, className:"userMes"})
    },
    messageShow() {
      this.isMessageComing = false;
      this.chat = true;
    }
  },
  store
}



