import store from '../../assets/js/shareData/share'
import '../../assets/js/base'
import Tou from '../Top/top'
  export default {
      name: "pay",
    components:{
      Tou
    },
    sockets:{
      connect() {
        this.$socket.emit("groupId", this.oid)
        this.$socket.emit(this.oid + "chatGroup")
      }
    },
    created:function() {
        this.getUser();
        console.log(this.$route.params.payOrder)
        if(this.$route.params.payOrder) {
          this.order.driverName = this.$route.params.payOrder.driverName
          this.order.driverPhoneNum = this.$route.params.payOrder.driverPhoneNum
          this.order.realCost = this.$route.params.payOrder.realCost
          this.order.oid = this.$route.params.payOrder.oid
          this.order.discount = this.$route.params.payOrder.discount
          this.order.originCost = this.$route.params.payOrder.originCost
        }

    },
    data:function() {
        return{
          order:{
            driverName:'',
            driverPhoneNum:'',
            oid:'',
            realCost:Number,
            commentScore:5,
            discount:0,
            originCost:0
          },
          liList:[{active:true},{active:true},{active:true},{active:true},{active:true}]
        }
    },
    methods:{
        comment(index) {
          for(var i = 0; i < this.liList.length; i++){
            this.liList[i].active = false;
          }
          for(var j = 0; j <= index; j++) {
            this.liList[j].active = true;
          }
          this.order.commentScore = index + 1;
         },
        submit() {
          this.$socket.emit("commentMsg", {commentScore:this.order.commentScore})
          let This = this;

          this.$http.post(this.$store.state.baseUrl + "complete", {oid:this.order.oid,
            realCost:this.order.realCost, driverName:this.order.driverName,commentScore:this.order.commentScore,discount:this.order.discount}).then(function(res){
               if(res.data) {
                 console.log(res.data)
                 This.$router.push("/")
               }else {
                 console.log("false")
               }
          })


        },
      getUser() {
        this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
          if(typeof response.data == "object") {
            this.$store.state.user = response.data
          }
        })
      }
    },
    store
  }



