import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
    name: '',
    data(){
      return {
        oldPsw:'',
        newPsw:'',
        againNewPsw:'',
        pswErr:''
      }
    },
    methods:{
      editPsw(){
        if(this.oldPsw==''){
          this.pswErr='旧密码不能为空';
        }else {
          if(this.newPsw == this.againNewPsw){
            this.$http.post(this.$store.state.baseUrl+"editPsw",{
              newPassword:this.newPsw,
              oldPassword:this.oldPsw
            },{emulateJSON:true}).then((response) => {
              if(response.data){
                alert('密码已修改');
                this.$router.push('/personalCenter');
              }else {
                this.pswErr='密码不正确，请重新输入';
              }
            })
          }else {
            this.pswErr='两次密码不一致';
          }
        }

      },
      deletePsw(index){
        if(index == 3){
          this.againNewPsw = '';
          return false;
        }
        if(index == 2){
          this.newPsw = '';
          return false;
        }
        if(index == 1 ){
          this.oldPsw = '';
        }
      }
    },
    store

  }



