import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
import Prompt from '../tips/tankuang'
  export default {
      name: '',
    components:{
      Prompt
    },
    data(){
      return {
        error:'',
        couponCode:'',
      }
    },
    methods:{
      getCoupon(){
        if(this.couponCode){
          this.$http.get(this.$store.state.baseUrl+'getCoupon',{params:{_id:this.couponCode}},{emulateJSON:true}).then(res=>{
            if(res.data.status){
              this.$router.push('/coupon')
            }else {
              this.error = res.data.msg;
              setTimeout(()=>{
                this.error = '';
              },1500);
            }
          })
        }else {
          this.error = '请输入优惠码';
          setTimeout(()=>{
            this.error = '';
          },1500);
        }
      }
    },
    store
  }



