
import Prompt from '../tips/tankuang'
import XHeader from 'vux/src/components/x-header/index.vue'

import store from '../../assets/js/shareData/share'
export default {
components:{
Prompt,
XHeader,
},
data:function() {
return {
name:'',
moreCommonAddress:[],
address:'',
isShow:false,
historyList:[],
isOnAddress:false,
isOffAddress:false,
error:'',
flag:false
}
},
created:function() {
this.getParams();
this.$http.get(this.$store.state.baseUrl+"custom").then(function(response) {
console.log(response.data)
if(response.data) {
console.log("shuju: " + response.data)
this.historyList = response.data.historyOrders;
console.log("historyList: " + this.historyList)
this.moreCommonAddress = response.data.commonAddress;
}
})

},
methods:{
getParams() {
console.log(this.$route.query.onAddress)
if(this.$route.query.onAddress) {
console.log("onAddress")
this.address = this.$route.query.onAddress
this.isOnAddress = true
console.log(this.isOnAddress)
}
if(this.$route.query.offAddress) {
console.log("offAddress")
this.address = this.$route.query.offAddress
this.isOffAddress = true
}
},
toUpMore(index){
this.address = this.moreCommonAddress[index].address
/*this.$http.post(this.$store.state.baseUrl+"toStep",{onAddress:this.onAddress},{emulateJSON: true}).then(function(res){
if(res.data) {
this.$router.push("/step");
}
})*/
},
toUpHistory(index){
if(this.isOnAddress) {
this.address = this.historyList[index].onAddress
}
if(this.isOffAddress) {
this.address = this.historyList[index].offAddress;
}
/*this.$http.post(this.$store.state.baseUrl+"toStep",{onAddress:this.onAddress},{emulateJSON: true}).then(function(res){
if(res.data) {
this.$router.push("/step");
}
})*/
},
autoSearch() {
console.log("ffef")
this.isShow = true;
},
validate() {
if(this.name && this.address) {
this.$http.post(this.$store.state.baseUrl+"addCommon", {
name:this.name,
address:this.address
},{emulateJSON:true}).then(function(response) {
if(response.data == "true") {
if(this.isOnAddress) {
this.$router.push("/getOn")
}
if(this.isOffAddress) {
this.$router.push("/getOff")
}
}
})
this.error = ""
this.flag = false;
}else {
this.error = "请填写自定义名称"
this.flag = true;
}
}
},
store
}



