import store from '../../assets/js/shareData/share'
import Top from '../Top/top'
  export default {
      name: "cancel-success",
    components:{
      Top
    },
    created() {
       if(this.$route.params.onTime) {
         this.onTime = this.$route.params.onTime
         this.getOn = this.$route.params.getOn
         this.getOff = this.$route.params.getOff
         this.realCost = this.$route.params.realCost
         this.minCost = this.$route.params.minCost
         console.log("minCost.............." + this.$route.params.minCost)
         console.log("realCost.............." + this.$route.params.realCost)
         this.maxCost = this.$route.params.maxCost
         this.isTakingOrder = this.$route.params.isTakingOrder
         console.log(this.$route.params.isTakingOrder)
       }
    },
    data:function() {
      return {
        onTime:'',
        getOn:'',
        getOff:'',
        realCost:'',
        minCost:'',
        maxCost:'',
        isTakingOrder:false
      }
    },
    methods: {
        toIndex(){
          this.$router.push("/")
      }
    },
      store
  }



