import store from '../../assets/js/shareData/share'
import "../../assets/js/base.js"
  export default {
      name: '',
    data(){
        return {
          starCount:[],
          showPrice:false
        }
    },
    mounted(){
      this.send();
    },
    methods:{
      send(){
        if(this.$store.state.hisOrderDetails.oid){
          for(let i=0;i<this.$store.state.hisOrderDetails.commentScore;i++){
            this.starCount.push(i);
          }
        }else{
          this.$router.push('/historyOrders')
        }
      },
      toHelp(){
          this.$router.push('/orderHelp');
      }
    },
    filters:{
      urlHandle(value,url){
        if(!value) return '';
        return url+'avatar/'+value;
      },
      currency(value){
        if(!value) return 0.00;
        let num = value.toString();
        let valArr = num.split('.');
        if(valArr.length<=1){
          return num+'.00';
        }else if(valArr[1].length<2){
          return num+'0';
        }else {
          return valArr[0]+'.'+valArr[1].substring(0,2);
        }
      }
    },
    store
  }



