import {Button,DateTimePicker,Main} from 'element-ui'
import store from '../../assets/js/shareData/share'
import Tou from '@/components/public/header'
import Prompt from '../tips/tankuang'
    export default {
      components: {
        Button,
        DateTimePicker,
        Tou,
        Main,
        Prompt
      },
      data() {
        return {
          form: {
            name: '',
            region: '',
          },
          pickerOptions1: {
            shortcuts: [{
              text: '今天',
              onClick(picker) {
                picker.$emit('pick', new Date());
              }
            }, {
              text: '昨天',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24);
                picker.$emit('pick', date);
              }
            }, {
              text: '一周前',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', date);
              }
            }]
          },
          error:'',
          flag:false,
          getOn: '',
          getOff: '',
          onTime:'',
          isBackAndForth:false,
        }
      },
      created:function() {
        this.getUser()
      },
      mounted:function(){
        this.onTime = this.$store.state.onTime
        this.getOn = this.$store.state.getOn
        console.log("getOn.........." + this.getOn)

        this.getOff = this.$store.state.getOff
        console.log("getOff.........." + this.getOff)
      },
      methods:{
        validate:function() {
          if (!this.onTime) {
            this.flag = true;
            this.error = "请输入用车时间"
            let timer = setTimeout(()=>{
              this.error = ""
              clearTimeout(timer);
            },1000)
            return
          }
          if (!this.getOn) {
            this.flag = true;
            this.error = "请输入上车地点"
            let timer = setTimeout(()=>{
              this.error = ""
              clearTimeout(timer);
            },1000);
            return
          }
          if (!this.getOff) {
            this.flag = true;
            this.error = "请输入下车地点"
            let timer = setTimeout(()=>{
              this.error = ""
              clearTimeout(timer);
            },1000)
            return
          }
          console.log("this.$store.onTime:  " + this.$store.state.onTime)
          this.sendData()
        },
        sendData() {
          this.$http.post(this.$store.state.baseUrl+"order", {
            onTime: this.onTime.toString(),
            getOn: this.getOn,
            getOff: this.getOff,
            isBackAndForth:this.isBackAndForth
          }, {emulateJSON: true}).then((response) => {
            console.log(response.data)
            if (typeof response.data == "object") {
              console.log("response data : " + response.data.oid);
              this.$router.push({
                path:"/step1",
                query:{
                  oid: response.data.oid
                }
              })
            } else {
              this.flag = true;
              this.error = response.data
            }
          })
        },
        getUser() {
          this.$http.get(this.$store.state.baseUrl+"getState").then(function(response) {
            if(typeof response.data == "object") {
              this.$store.state.user = response.data
            }
          })
        },
        preserve() {
          this.$store.state.onTime = this.onTime;
        },
        toGetOn() {
          this.$router.push({
            path:"/getOn",
          })
        },
        toGetOff() {
          this.$router.push({
            path:"/getOff",
          })
        }
      },
      store
    }



