import store from '../../assets/js/shareData/share'
import '../../assets/js/base'
  export default {
    name: '',
    data(){
      return {
        email:'',
        emailError:''
      }
    },
    methods:{
      editEmail(){
        let emailReg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        if(this.email){
          if(emailReg.test(this.email)){
            this.$http.post(this.$store.state.baseUrl+"editEmail",{
              email:this.email
            },{emulateJSON:true}).then((response) => {
              alert(response.data);
            })
          }else {
            this.emailError = '邮箱格式不正确';
          }
        }else {
          this.emailError = '邮箱不能为空';
        }
      }
    },
    store
  }



