// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';//若发现该处导包总是出错，请检查node_modules中的对应版本目录下是否有theme-default/index.css文件，若没有，卸载element-ui重新安装低版本element,并修改package.json中element-ui版本
import vuex from 'vuex'
import VueSocketio from 'vue-socket.io';
import DrawerLayout from 'vue-drawer-layout'
import VueJsonp from 'vue-jsonp'
Vue.use(VueJsonp)

let url1 = 'http://fooldman.com:8090';
let url2 = 'http://lh666.club:8090'
Vue.use(DrawerLayout)
Vue.use(vuex)
Vue.config.productionTip = false;
Vue.use(VueResource);
Vue.use(ElementUI);
Vue.use(VueSocketio, url1)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});



