import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
      name: "find-pwd",
      data:function() {
        return {
          newPwd:'',
          error:'',
          flag:false,
          repeatPwd:'',
          passwordShow:true,
          userShow:true
        }
      },
      methods:{
        validate() {
          if (!this.newPwd) {
            this.error = "请填写密码"
            this.flag = true;
            return;
          }
          let pPattern = /^[a-zA-Z0-9]{5,15}$/;
          if (!pPattern.test(this.newPwd)) {
            this.error = "您输入的密码不符合规定（密码由：数字、字母、下划线、特殊符号组成,长度必须大于5位)"
            this.flag = true;
            return;
          }
          if(!this.repeatPwd) {
            this.error = "请确认密码"
            this.flag = true;
            return;
          }
          if(this.newPwd !== this.repeatPwd) {
            this.error = "两次输入的密码不一致"
            console.log("密码不一致")
            this.flag = true;
            return;
          }
          this.flag = false;
          this.sendData()
        },
        sendData() {
          this.$http.post(this.$store.state.baseUrl+"reLogin", {
            newPwd:this.newPwd
          }, {emulateJSON: true}).then((response) => {
            if (response.data == "true") {
              console.log(response.data);
              this.$router.push("/modifySuccess")
            } else {
              this.flag = true;
              this.error = response.data
            }
          })
        },
        isUserShow() {
          if(!this.newPwd) {
            this.userShow = true;
          }
        },
        isPasswordShow() {
          if(!this.repeatPwd) {
            this.passwordShow = true;
          }
        }
      },
    store
  }



